import React, { FC, useState, useRef } from "react";
import cn from "classnames/bind";

import { useClickOutside } from "common/hooks/useClickOutside";
import { PageTheme } from "common/constants/enum";
import InfoIcon from "common/components/Icons/icons/24/info.svg";

import { TooltipProps } from "./Tooltip.types";
import styles from "./Tooltip.module.scss";

const cx = cn.bind(styles);

export const Tooltip: FC<TooltipProps> = ({
    className,
    children,
    theme = PageTheme.WHITE,
}) => {
    const buttonRef = useRef<HTMLButtonElement>(null);

    const [show, setShow] = useState(false);

    const onButtonClick = () => {
        setShow(!show);
    };

    useClickOutside(buttonRef, () => {
        setShow(false);
    });

    return (
        <>
            <button
                className={cx("button", className, `theme_${theme}`)}
                onClick={onButtonClick}
                ref={buttonRef}
            >
                <InfoIcon />
            </button>
            {show && <div className={cx(`theme_${theme}`)}>{children}</div>}
        </>
    );
};
