import { FC, useContext, useEffect, useRef, useState } from "react";
import classNames from "classnames/bind";

import { HowItWorksTypes } from "common/components/HowItWorks/hooks/reducers";
import { HowItWorksContext } from "common/components/HowItWorks/hooks/context";
import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";

import { AccordionItemProps } from "./AccordionItem.types";

import styles from "./AccordionItem.module.scss";

const cx = classNames.bind(styles);

export const AccordionItem: FC<AccordionItemProps> = ({ index, content }) => {
    const { title, description, isActive } = content;
    const [isReady, setReady] = useState(false);

    const activeItem = useRef<HTMLDivElement>(null);
    const { dispatch } = useContext(HowItWorksContext);

    useEffect(() => {
        setReady(true);
    }, [activeItem.current]);

    const height =
        (isReady &&
            isActive &&
            activeItem &&
            activeItem.current &&
            activeItem.current.clientHeight) ||
        0;

    const handleAccordionClick = () => {
        dispatch({
            type: HowItWorksTypes.SetActiveStage,
            payload: index,
        });
    };

    return (
        <div className={cx("root")}>
            <div className={cx("topic")} onClick={handleAccordionClick}>
                <Heading
                    tag={HeadingTag.H3}
                    type={HeadingType.S}
                    className={cx("title", isActive && "title_open")}
                >
                    {title}
                </Heading>
            </div>
            <div className={cx("description")} style={{ height }}>
                <div
                    className={cx("inner", isActive && "inner_open")}
                    ref={activeItem}
                >
                    <p className={cx("description__text")}>{description}</p>
                </div>
            </div>
        </div>
    );
};
