import { FC } from "react";
import classNames from "classnames/bind";

import { StagesHowItWorksProps } from "common/components/HowItWorks";

import styles from "./Progress.module.scss";

const cx = classNames.bind(styles);

export const Progress: FC<StagesHowItWorksProps> = ({ stages, className }) => (
    <div className={cx(className, "root")}>
        {stages.map(({ isActive, videoDuration }, index) => {
            return (
                <div
                    key={index}
                    className={cx("meter", {
                        "meter_is-active": isActive,
                    })}
                >
                    <span
                        className={cx("progress-container", {
                            "progress-container_is-active": isActive,
                        })}
                    >
                        <span
                            className={cx("progress")}
                            style={{
                                animationDuration: `${videoDuration}s`,
                            }}
                        />
                    </span>
                </div>
            );
        })}
    </div>
);
