import { FC } from "react";
import classNames from "classnames/bind";

import { StagesHowItWorksProps } from "common/components/HowItWorks";

import { StageVideoElement } from "../StageVideoElement";

import styles from "./StagesVideos.module.scss";

const cx = classNames.bind(styles);

export const StagesVideos: FC<StagesHowItWorksProps> = ({
    stages,
    className,
}) => (
    <div className={cx(className, "root")}>
        {stages.map((stage, index) => (
            <StageVideoElement key={index} {...stage} />
        ))}
    </div>
);
