import { getCookiesValueByName, setCookie } from "common/utils/cookie";
import { COOKIES, ThirtyMinutesInSeconds } from "common/constants/enum";

export const getValueOfCookieTduid = () =>
    decodeURIComponent(getCookiesValueByName(COOKIES.TRADEDOUBLER_ID));

export const setValueOfCookieTduid = (value?: string) => {
    setCookie(COOKIES.TRADEDOUBLER_ID, value || "", {
        path: "/",
        "max-age": ThirtyMinutesInSeconds,
    });
};
