import { FC } from "react";
import classNames from "classnames/bind";

import { Video } from "common/components/Video";
import { StageHowItWorksProps } from "common/components/HowItWorks";

import styles from "./StageVideoElement.module.scss";

const cx = classNames.bind(styles);

export const StageVideoElement: FC<StageHowItWorksProps> = ({
    linkVideo,
    isActive,
}) => {
    return (
        <div className={cx("root", { "root_is-active": isActive })}>
            <div className={cx("video-content")}>
                {linkVideo && (
                    <Video
                        className={cx("video", {
                            "video_is-active": isActive,
                        })}
                        autoPlay={true}
                        loop={true}
                        muted={true}
                        playsInline={true}
                        src={linkVideo}
                    />
                )}
            </div>
        </div>
    );
};
