import { FC, useState, useEffect } from "react";
import classNames from "classnames/bind";

import { Picture } from "common/components/Picture";
import { Tabs, TabType } from "common/components/Tabs";
import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import { Text } from "common/components/Text";

import { ImageType, OurClinicsWithTabsProps } from "./OurClinicsWithTabs.types";
import styles from "./OurClinicsWithTabs.module.scss";

const cx = classNames.bind(styles);

export const OurClinicsWithTabs: FC<OurClinicsWithTabsProps> = ({
    images,
    title,
    subtitle,
    tabs,
    className,
    currentCity,
}) => {
    const [activeCity, setActiveCity] = useState<TabType>();
    const [activeTab, setActiveTab] = useState<TabType>(tabs[0]);
    const [sortedTabs, setSortedTabs] = useState(tabs);

    useEffect(() => {
        if (activeCity) {
            resortTabs();
        }
    }, [activeCity]);

    useEffect(() => {
        if (currentCity) {
            const tabItem = tabs.find((item) => item.title === currentCity);
            if (tabItem) {
                setActiveCity(tabItem);
                setActiveTab(tabItem);
            }
        }
    }, [currentCity]);

    const onTabChanged = (option: TabType) => {
        setActiveTab(option);
    };

    const resortTabs = () => {
        if (activeCity?.value) {
            tabs.sort((a: TabType) => {
                if (a.value === activeCity.value) {
                    return -1;
                } else {
                    return 1;
                }
            });
            setSortedTabs(() => [...tabs]);
        }
    };

    return (
        <section className={cx(className, "root")}>
            <div className={cx("header")}>
                <Heading
                    className={cx("title", {
                        "title_with-subtitle": !!subtitle,
                    })}
                    tag={HeadingTag.H4}
                    type={HeadingType.M}
                >
                    {title}
                </Heading>
                {subtitle && <Text className={cx("subtitle")}>{subtitle}</Text>}
                {sortedTabs?.length && sortedTabs?.length > 1 && (
                    <Tabs
                        classNameScroll={cx("tabs")}
                        classNameContent={cx("tabs-content")}
                        tabs={sortedTabs}
                        onChange={onTabChanged}
                        initialTab={activeTab}
                    />
                )}
            </div>
            <div className={cx("images")}>
                {images[activeTab.value as keyof ImageType]?.map(
                    (image, index) => {
                        return (
                            <Picture
                                {...image}
                                key={index}
                                className={cx("picture", {
                                    [`picture_${index + 1}`]: index + 1,
                                })}
                                classNameImg={cx("image")}
                            />
                        );
                    }
                )}
            </div>
        </section>
    );
};
