import { FC, useContext, useEffect } from "react";
import classNames from "classnames/bind";

import { Heading, HeadingTag } from "common/components/new/Heading";

import { HowItWorksContext } from "./hooks/context";
import { HowItWorksTypes } from "./hooks/reducers";
import { Progress } from "./components/Progress";
import { StagesImages } from "./components/StagesImages";
import { StagesVideos } from "./components/StagesVideos";
import { AccordionList } from "./components/AccordionList";

import { HowItWorksContentProps } from "./HowItWorks.types";

import styles from "./HowItWorks.module.scss";

const cx = classNames.bind(styles);

export const HowItWorks: FC<HowItWorksContentProps> = ({
    title,
    stages,
    className,
}) => {
    const { state, dispatch } = useContext(HowItWorksContext);

    const activeStage = state.stages.find((stage) => stage.isActive);

    useEffect(() => {
        if (stages.length) {
            const setDataReducer = () => {
                dispatch({
                    type: HowItWorksTypes.SetStages,
                    payload: stages,
                });
            };

            setDataReducer();
        }
    }, [stages.length]);

    useEffect(() => {
        if (activeStage) {
            const timeShowStageInMs = activeStage.videoDuration * 1000;
            const interval = setInterval(() => {
                dispatch({
                    type: HowItWorksTypes.SetNextActiveStage,
                });
            }, timeShowStageInMs);
            return () => clearInterval(interval);
        }
    }, [activeStage]);

    return (
        <div className={cx("root", className)}>
            <Heading tag={HeadingTag.H2} className={cx("title")}>
                {title}
            </Heading>

            <div className={cx("content")}>
                <div className={cx("column", "column-images")}>
                    <StagesImages stages={state.stages} />
                </div>
                <div className={cx("column", "column-content")}>
                    <div className={cx("info")}>
                        <StagesVideos
                            stages={state.stages}
                            className={cx("video")}
                        />

                        <div className={cx("info-stages")}>
                            <AccordionList
                                stages={state.stages}
                                className={cx("accordion-list")}
                            />
                            <Progress
                                stages={state.stages}
                                className={cx("progress")}
                            />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};
