import { Dispatch, SetStateAction } from "react";

import { Direction } from "common/constants/enum";

export const sideScroll = (
    element: HTMLDivElement | null,
    direction: "left" | "right",
    speed: number,
    distance: number,
    step: number,
    isDisabledPrevButton?: Dispatch<SetStateAction<boolean>>,
    isDisabledNextButton?: Dispatch<SetStateAction<boolean>>,
    maxWidth?: number
) => {
    let scrollAmount = 0;

    if (element) {
        const slideTimer = setInterval(() => {
            if (direction == Direction.LEFT) {
                element.scrollLeft -= step;
            } else {
                element.scrollLeft += step;
            }

            if (isDisabledPrevButton) {
                isDisabledPrevButton(element.scrollLeft === 0);
            }

            if (isDisabledNextButton && maxWidth) {
                isDisabledNextButton(element.scrollLeft >= maxWidth);
            }

            scrollAmount += step;

            if (scrollAmount >= distance) {
                window.clearInterval(slideTimer);
            }
        }, speed);
    }
};
