import { FC } from "react";
import classNames from "classnames/bind";

import { Text, TextTag } from "common/components/Text";
import { Button, ButtonSize } from "common/components/new/Button";
import { Tooltip } from "common/components/Tooltip";
import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";

import MainContent from "./components/MainContent/MainContent";
import { PaymentOnce } from "./components/PaymentOnce";
import { PaymentMonthly } from "./components/PaymentMonthly";
import { PriceBlockProps, OfferProgressProps } from "./PriceBlock.types";
import styles from "./PriceBlock.module.scss";

const cx = classNames.bind(styles);

export const PriceBlock: FC<PriceBlockProps> = ({
    month,
    period,
    button,
    currency,
    duration,
    version,
    className,
    legalPrice,
    buttonLink,
    prePayment,
    treatments,
    legalSorteo,
    payTypeOnce,
    offerRegular,
    offerExtended,
    onButtonClick,
    payTypeMonthly,
    monthlyPaymentHelperText,
    isMobileButtonHide,
    durationDescription,
    progressTitle,
    firstVisitPayment,
}) => {
    const buttonAction = onButtonClick
        ? { onClick: onButtonClick }
        : { href: buttonLink };

    const renderToothItem = ({ title, icon }: OfferProgressProps) => (
        <div className={cx("teeth__item")} key={title}>
            <div className={cx("teeth__icon")}>
                <img src={icon} alt={title} width="78" height="40" />
            </div>
            <Text className={cx("teeth__title")}>{title}</Text>
        </div>
    );

    return (
        <div className={cx("root", className)}>
            <div
                className={cx(
                    "merged-column",
                    "treatment",
                    "treatment_with-divider"
                )}
            >
                <Heading
                    tag={HeadingTag.H3}
                    type={HeadingType.M}
                    className={cx("treatment__title", "only-desktop")}
                >
                    {treatments}
                </Heading>
                <div
                    className={cx("button-container", "only-desktop", {
                        "button-container_hide-mobile": isMobileButtonHide,
                    })}
                >
                    <Button
                        className={cx("button")}
                        size={ButtonSize.M}
                        {...buttonAction}
                    >
                        {button}
                    </Button>
                </div>
            </div>
            <div
                className={cx("column", "column_with-divider", "main-content")}
            >
                <MainContent {...offerRegular} />
            </div>
            <div className={cx("column", "column_extended")}>
                <MainContent {...offerExtended} />
            </div>
            {offerRegular.description && (
                <Text
                    className={cx(
                        "column",
                        "column_regular",
                        "column_with-divider",
                        "description",
                        "only-desktop"
                    )}
                >
                    {offerRegular.description}
                </Text>
            )}
            {offerExtended.description && (
                <Text
                    className={cx(
                        "column",
                        "column_extended",
                        "description",
                        "only-desktop"
                    )}
                >
                    {offerExtended.description}
                </Text>
            )}
            {progressTitle && (
                <Text
                    className={cx(
                        "merged-column",
                        "progress-title",
                        "only-mobile"
                    )}
                >
                    {progressTitle}
                </Text>
            )}
            {offerRegular.progress && (
                <div
                    className={cx(
                        "column",
                        "column_regular",
                        "column_with-divider",
                        "column_with-divider-teeth",
                        "teeth"
                    )}
                >
                    {offerRegular.progress?.map(renderToothItem)}
                </div>
            )}
            {offerExtended.progress && (
                <div className={cx("column", "column_extended", "teeth")}>
                    {offerExtended.progress?.map(renderToothItem)}
                </div>
            )}
            <div className={cx("duration", "merged-column", "only-desktop")}>
                <Heading
                    type={HeadingType.M}
                    tag={HeadingTag.H3}
                    className={cx("duration__title")}
                >
                    {duration}
                </Heading>
                <Text className={cx("duration__description")}>
                    {durationDescription}
                </Text>
            </div>
            <div
                className={cx(
                    "column",
                    "duration-value",
                    "column_with-divider"
                )}
            >
                <span
                    className={cx("only-mobile")}
                    dangerouslySetInnerHTML={{
                        __html: offerRegular.durationMobile,
                    }}
                ></span>
                <span className={cx("only-desktop")}>
                    {offerRegular.duration}
                </span>
            </div>
            <div className={cx("column", "column_extended", "duration-value")}>
                <span
                    className={cx("only-mobile")}
                    dangerouslySetInnerHTML={{
                        __html: offerExtended.durationMobile,
                    }}
                ></span>
                <span className={cx("only-desktop")}>
                    {offerExtended.duration}
                </span>
            </div>
            <div
                className={cx("merged-column", "payment", {
                    "payment_with-teeth": offerRegular.description,
                    "payment_with-old-price":
                        offerExtended.paymentOnce.oldPrice,
                })}
            >
                <Heading
                    tag={HeadingTag.H3}
                    type={HeadingType.M}
                    className={cx("payment", "payment_monthly", "only-desktop")}
                >
                    {payTypeMonthly}
                </Heading>
                <Text
                    tag={TextTag.Span}
                    className={cx("payment", "payment_monthly", "only-mobile")}
                >
                    {payTypeMonthly}
                    {monthlyPaymentHelperText && (
                        <Tooltip className={cx("helper", "only-mobile")}>
                            <Text
                                tag={TextTag.Span}
                                className={cx("helper__content")}
                            >
                                {monthlyPaymentHelperText}
                            </Text>
                        </Tooltip>
                    )}
                </Text>
                {legalSorteo && (
                    <Text
                        className={cx(
                            "merged-column",
                            "legal",
                            "legal_sorteo",
                            {
                                "legal_with-teeth": offerRegular.description,
                            }
                        )}
                    >
                        {legalSorteo}
                    </Text>
                )}
            </div>
            <PaymentMonthly
                className={cx(
                    "column",
                    "column_flex",
                    "column_with-divider",
                    "column__payment-monthly"
                )}
                payment={offerRegular.paymentMonthly}
                month={month}
                period={period}
                version={version}
                currency={currency}
                prePayment={prePayment}
            />
            <PaymentMonthly
                className={cx(
                    "column",
                    "column_flex",
                    "column_extended",
                    "column__payment-monthly"
                )}
                payment={offerExtended.paymentMonthly}
                month={month}
                period={period}
                version={version}
                currency={currency}
                prePayment={prePayment}
            />
            <Heading
                tag={HeadingTag.H3}
                type={HeadingType.M}
                className={cx("merged-column", "payment", "only-desktop", {
                    "payment_once-with-teeth": offerRegular.description,
                    payment_once: offerExtended.paymentOnce.oldPrice,
                })}
            >
                {payTypeOnce}
            </Heading>

            <PaymentOnce
                className={cx(
                    "column",
                    "column_with-divider",
                    "column__payment-once"
                )}
                payment={offerRegular.paymentOnce}
                currency={currency}
                version={version}
            />

            <PaymentOnce
                className={cx(
                    "column",
                    "column_extended",
                    "column__payment-once"
                )}
                payment={offerExtended.paymentOnce}
                currency={currency}
                version={version}
            />

            {legalPrice && (
                <Text
                    className={cx("merged-column", "legal", "legal_price", {
                        "legal_price-with-teeth": offerRegular.description,
                    })}
                >
                    {legalPrice}
                </Text>
            )}

            {firstVisitPayment && (
                <div className={cx("prices-footer", "merged-column")}>
                    <div className={cx("fv-prepayment")}>
                        <Text>{firstVisitPayment}</Text>
                    </div>
                </div>
            )}
        </div>
    );
};
