import React, { FC } from "react";

import { VideoProps } from "./Video.types";

export const Video: FC<VideoProps> = ({
    className,
    controls = false,
    poster = "",
    src,
    ...restProps
}) => (
    <video
        className={className}
        controls={controls}
        poster={poster}
        {...restProps}
    >
        <source src={src} type="video/mp4" />
        <a href={src}>Download video</a>
    </video>
);
