import cloneDeep from "clone-deep";

import { StageHowItWorksProps } from "common/components/HowItWorks";

type ActionMap<M extends { [index: string]: any }> = {
    [Key in keyof M]: M[Key] extends undefined
        ? {
              type: Key;
          }
        : {
              type: Key;
              payload: M[Key];
          };
};

export enum HowItWorksTypes {
    SetStages = "SET_STAGES",
    SetNextActiveStage = "SET_NEXT_ACTIVE_STAGE",
    SetPrevActiveStage = "SET_PREV_ACTIVE_STAGE",
    SetActiveStage = "SET_ACTIVE_STAGE",
}

type StagePayload = {
    [HowItWorksTypes.SetStages]: StageHowItWorksProps[];
    [HowItWorksTypes.SetNextActiveStage]: undefined;
    [HowItWorksTypes.SetPrevActiveStage]: undefined;
    [HowItWorksTypes.SetActiveStage]: number;
};

export type StageActions = ActionMap<StagePayload>[keyof ActionMap<StagePayload>];

export const stageReducer = (
    state: StageHowItWorksProps[],
    action: StageActions
) => {
    const newState = cloneDeep(state);
    const indexOfActiveStage = state.findIndex((stage) => stage.isActive);

    switch (action.type) {
        case HowItWorksTypes.SetStages:
            return action.payload;

        case HowItWorksTypes.SetNextActiveStage:
            newState[indexOfActiveStage].isActive = false;

            if (indexOfActiveStage !== newState.length - 1) {
                newState[indexOfActiveStage + 1].isActive = true;
            } else {
                newState[0].isActive = true;
            }

            return newState;

        case HowItWorksTypes.SetPrevActiveStage:
            newState[indexOfActiveStage].isActive = false;

            if (indexOfActiveStage !== 0) {
                newState[indexOfActiveStage - 1].isActive = true;
            } else {
                newState[newState.length - 1].isActive = true;
            }

            return newState;

        case HowItWorksTypes.SetActiveStage:
            newState.forEach((state, index) => {
                if (indexOfActiveStage !== action.payload) {
                    state.isActive = index === action.payload;
                }
            });

            return newState;

        default:
            return state;
    }
};
