import { FC, createContext, useReducer, Dispatch } from "react";

import { StageHowItWorksProps } from "common/components/HowItWorks";

import { stageReducer, StageActions } from "./reducers";

interface InitialStateType {
    stages: StageHowItWorksProps[];
}

const initialState = {
    stages: [],
};

const HowItWorksContext = createContext<{
    state: InitialStateType;
    dispatch: Dispatch<StageActions>;
}>({
    state: initialState,
    dispatch: () => null,
});

const mainReducer = ({ stages }: InitialStateType, action: StageActions) => ({
    stages: stageReducer(stages, action),
});

const HowItWorksProvider: FC = ({ children }) => {
    const [state, dispatch] = useReducer(mainReducer, initialState);

    return (
        <HowItWorksContext.Provider value={{ state, dispatch }}>
            {children}
        </HowItWorksContext.Provider>
    );
};

export { HowItWorksContext, HowItWorksProvider };
