import { FC, useState, useEffect } from "react";
import classNames from "classnames/bind";

import { HorizontalScroll } from "common/components/HorizontalScroll";

import { TabsProps, TabType } from "./Tabs.types";
import styles from "./Tabs.module.scss";

const cx = classNames.bind(styles);

export const Tabs: FC<TabsProps> = ({
    tabs,
    initialTab,
    classNameScroll,
    classNameContent,
    classNameTab,
    onChange,
}) => {
    const [activeTab, setActive] = useState<TabType>(initialTab || tabs[0]);

    useEffect(() => {
        if (initialTab) {
            setActive(initialTab);
        }
    }, [initialTab]);

    const handleTabClick = (option: TabType) => () => {
        setActive(option);
        onChange(option);
    };

    return (
        <HorizontalScroll className={classNameScroll}>
            <div className={cx(classNameContent, "content")}>
                {tabs.map((option: TabType) => (
                    <button
                        className={cx(classNameTab, "tab", {
                            tab_active: activeTab.value === option.value,
                        })}
                        type="button"
                        key={option.value}
                        onClick={handleTabClick(option)}
                    >
                        {option.title}
                    </button>
                ))}
            </div>
        </HorizontalScroll>
    );
};
